.slider {
    &-landing {
        height: 550px;
        margin-bottom: 20px;
        overflow: hidden;
    }

    &-left {
        position: relative;
        height: 100%;
        max-height: 100%;
        width: 75%;
        float: left;

        img.empty-img {
            max-height: 550px;
            object-fit: cover;
        }

        @include media-breakpoint-down(lg) {
            width: 70%;
        }

        @include media-breakpoint-down(md) {
            width: 60%;
        }
    }

    &-nav {
        padding: 15px;
        background: $primary;
        color: #fff;
        width: 100%;
        display: flex;
        align-items: center;
        border-bottom: 1px solid #eee;
        transition: 0.2s ease-in-out;
        z-index: 9;


        &.active,
        &:hover {
            background: darken($primary, 10%);
            width: calc(100% + 15px);
            margin-left: -15px;
            position: relative;
        }


    }

    &-right {
        width: 25%;
        float: left;
        height: 100%;
        display: flex;
        flex-flow: row wrap;
        position: relative;

        @include media-breakpoint-down(lg) {
            width: 30%;
        }

        @include media-breakpoint-down(md) {
            width: 40%;
        }

        &-btn {
            position: absolute;
            right: 10px;
            bottom: 10px;
            z-index: 9;
            color: #fff;
        }

    }

    &-img {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        object-fit: cover;
    }

    &-detail {
        position: absolute;
        bottom: 0;
        background: rgba(255, 255, 255, 0.85);
        padding: 15px;
        z-index: 2;
        width: 100%;

        h4 {
            color: $primary;
            font-weight: bold;
            margin-bottom: 5px;
        }

        p {
            margin-bottom: 0;
        }


    }

}

