.slider-detail a,
.features-post a {
    margin-left: 5px;
}

.read-more {
    float: right;
    font-size: 14px;
    color: #999;
    font-style: italic;
    font-weight: bold;
    line-height: 40px;
}

.feature {
    &-img {
        width: 100%;
        height: 100%;
        margin-bottom: 15px;
        max-height: 550px;
        object-fit: cover;
    }
}

.my-app,
.app {
    padding: 10px;
    width: 16.6666667%;
    float: left;
    text-align: center;

    @include media-breakpoint-down(md) {
        width: 33.3333333%
    }

    &-container {
        padding: 0 30px;
    }

    &-row {
        display: flex;
        flex-wrap: wrap;
        margin-right: -40px;
        margin-left: -40px;
    }

    &-item {
        position: relative;
        cursor: pointer;
        text-align: center;
        display: inline-block;
       a{ @include clearfix();}

        .material-icons {
            position: absolute;
            right: -5px;
            top: 65px;
            font-size: 28px;
            background: #fff;
            border-radius: 50%;
            color: $primary-alt;
            z-index:9;
            &:hover{

                color:$primary;
            }
        }

        // &-add {
        //     z-index: 9;
        //     position: absolute;
        //     top: 50%;
        //     left: 50%;
        //     transform: translate(-50%, -50%);
        //     background: red;

        //     i {
        //         font-size: 30px;
        //         color: #fff;
        //     }
        // }

        img {
            max-width: 100%;
            height: 85px;
            transition: 0.25s ease-in-out;
            object-fit: contain;
        }
    }

    &-name {
        font-style: initial;
        width: 100%;
        padding: 5px 0;
        display: block;
        color: $primary;
        text-align: center;
        font-size: 16px;
        a:hover{
            text-decoration: none;
        }
    }
}


.app-img{
    position:relative;
    display:inline-block;
}

.my-app {
    &-item {
        text-align: center;

        > a {
            position: relative;
            text-decoration: none;
        }
    }

    &-name {
        i {
            color: $danger;
        }
    }
}

.nw-home {
    h3.text-primary {
        color: #00415E !important;
    }

    h4.text-primary {
        font-weight: bold;
    }
}

.post {
    &-title {
        font-size: 18px;
        margin-bottom: 20px;
    }

    &-preview {
        padding: 20px;
        border: 1px solid #ddd;
        background: #eee;

        &-img {
            text-align: center;

            img {
                max-height: 400px;
                margin-bottom: 10px;
            }

            &-caption {
                display: block;
                font-size: 12px;
                padding-bottom: 8px;
                margin-bottom: 15px;
                border-bottom: 1px solid #999;
            }

        }

        &-title {
            font-size: 20px;
        }

        &-date {
            font-size: 14px;
            margin-bottom: 15px;
        }
    }

    &-category {
        font-size: 14px;
    }


}

.news {
    &-title {
        font-size: 20px;
    }
}

.nw-landing-view {
    .info-tooltip.info-tooltip-lg {
        color: #000;
    }
}

.more-to-know {
    img {
        border: 2px solid $primary-alt
    }
}

.alert-custom {
    background: #04B2FF;
    color: #fff;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.16);
    border-radius: 0;
    font-style: italic;
    font-weight: bold;
}

#articleGrid .k-widget.k-grid {
    height: calc(100vh - 250px) !important;
}